import React, {useState, useEffect} from 'react'
import axios from 'axios';
import './App.css';
import Coin from './Coin.js';


function App() {

  const [coins, setCoins] = useState([]);
  const [search, setSearch] = useState('');


  useEffect(() => {
    axios.get(
 'https://api.coingecko.com/api/v3/coins/markets?vs_currency=SAR&order=market_cap_desc&per_page=50&page=1&sparkline=false'
      )
    .then(res =>{
      setCoins(res.data);
      console.log(res.data);
    }).catch(error => console.log(error))
  },[]);

  const handleChange = e => {
    setSearch(e.target.value);
  };

  const filteredCoins = coins.filter(coin =>
    coin.name.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="coin-app">
      <h4 className='app-text'> react api crypto tracker v1</h4>
      <div className='coin-search'>
       
      <h2 className='coin-text'> Search a currency</h2>
      <form>
        <input type="text" plaseholder="search" onChange={handleChange}
         className="coin-input"/>
      </form>
      </div> 

      {/* <Coin 
    key='0'
    name='Name'
    image='-'
    symbol='Symbol'
    marketcap=''
    volume='Volume'
    price='Price'
    priceChange={0}
     /> */}

{filteredCoins.map (coin => {
  return (
    <Coin 
    key={coin.id}
    name={coin.name}
    image={coin.image}
    symbol={coin.symbol}
    marketcap={coin.total_volume}
    volume={coin.market_cap}
    price={coin.current_price}
    priceChange={coin.price_change_percentage_24h}
     />
  )
})}


    </div>

    
  );
}

export default App;
